import { Component, ViewChild } from '@angular/core';
import { PontosModalPage } from '../pontos-modal/pontos-modal.page';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { PopoverController, ActionSheetController, ModalController, IonInfiniteScroll } from '@ionic/angular';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { Team } from 'src/models/Team';
import { User } from 'src/models/User';
import { ToastText, MenuText } from 'src/commons/Constants';
import { Router } from '@angular/router';

@Component({
  selector: 'app-grupos',
  templateUrl: './grupos.page.html',
  styleUrls: ['./grupos.page.scss'],
})
export class GruposPage {

  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;


  private readonly logHeader = "GruposPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public multicheck: boolean = false;
  private teams: Array<Team> = new Array<Team>();
  public teamList: Array<Team> = new Array<Team>();
  private searchTerm: string;
  private selectedUsers: Array<number> = new Array<number>();

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    public popoverController: PopoverController,
    private api: BackendApiProvider,
    public actionSheetController: ActionSheetController,
    private utils: Utils,
    public modalController: ModalController,
    private router: Router
  ) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.getTeams();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getTeams() {
    const logHeader = this.logHeader + "getUsers>>";
    Utils.log(logHeader);
    this.api.getTeams().then((teams: Array<Team>) => {
      this.teams = teams;
      this.teamList = this.teams;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(team: Team) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [{
        text: MenuText.ADD_POINTS,
        handler: () => {
          this.presentPopOver(team);
        }
      },
      {
        text: MenuText.REMOVE_POINTS,
        role: 'destructive',
        handler: () => {
          this.presentPopOverDel(team);
        }
      },
      {
        text: MenuText.CANCEL, role: 'cancel',
        handler: () => {
        }
      }]
    });
    await actionSheet.present();
  }


  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isVarInvalid(evt) || Utils.isVarInvalid(evt.srcElement)) {
      return;
    }
    const searchTerm = evt.srcElement.value;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.teamList = this.teams;
      return;
    }
    this.teamList = this.teams.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  teamChange(team: Team, checked: boolean) {
    if (this.selectedUsers.indexOf(team.getLiderId()) < 0) {
      this.selectedUsers.push(team.getLiderId());
    }
    for (let genUser of team.getMembros()) {
      let user: User = genUser as User;
      if (checked) {
        if (this.selectedUsers.indexOf(user.getId()) < 0) {
          this.selectedUsers.push(user.getId());
        }
      }
      else {
        this.selectedUsers.splice(this.selectedUsers.indexOf(user.getId()), 1);
      }
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async presentPopOver(team: Team = undefined) {
    let users = await this.getTeamUsers(team);
    const modal = await this.popoverController.create({
      component: PontosModalPage,
      componentProps: {
        users: users,
        remove: false,
        backdropDismiss: false,
      }
    });
    return await modal.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async presentPopOverDel(team: Team = undefined) {
    let users = await this.getTeamUsers(team);
    const modal = await this.popoverController.create({
      component: PontosModalPage,
      backdropDismiss: false,
      componentProps: {
        users: users,
        remove: true,

      },
    });
    return await modal.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async getTeamUsers(team: Team) {
    let users = new Array<number>();
    // if (!Utils.isArrayInvalid(this.selectedUsers)) {
    //   users = this.selectedUsers;
    // }
    // else {
    if (Utils.isArrayInvalid(users) || (!Utils.isArrayInvalid(users) && users.indexOf(team.getLiderId()) < 0)) {
      users.push(team.getLiderId());
    }
    if (!Utils.isArrayInvalid(team.getMembros())) {
      for (let genUser of team.getMembros()) {
        let user: User = genUser as User;
        if (users.indexOf(user.getId()) < 0) {
          users.push(user.getId());
        }
      }
    }
    else {
      let fullTeam = (await this.api.getTeamById(team.getId())) as Team;
      if (!Utils.isArrayInvalid(fullTeam.getMembros())) {
        for (let genUser of fullTeam.getMembros()) {
          let user: User = genUser as User;
          if (users.indexOf(user.getId()) < 0) {
            users.push(user.getId());
          }
        }
      }
    }
    // }
    return users;
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  loadData(event) {
    setTimeout(() => {
      console.log('Done');
      event.target.complete();
      if (this.teams.length == 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  navigateDashboard() {
    this.router.navigate(['admin', 'dashboard'])
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}